import React from 'react'
import * as _Util from '../store/Util'




const RowViewPort = (props) => {
  const {  data,  typeBrowse, keyCode, _key_, sortList, Child } = props; 
  let _idK = `_**_${keyCode}_${_key_}_row_`;


  let arr = []
  if(sortList){
    arr = sortList;
  }else{
    arr = _Util.ObjectKeys(data);
  }

  return (    
    <div  id={_idK} className={`_dsplFlx spaceAround _flxWrp`}>
    {arr && arr.map((_grTGid,_grTGid_ind)=>{
      let _item = data[_grTGid];
      return (
        <>
          {Child?
            <Child _item={_item} _Id={_grTGid} indX={_grTGid_ind} _idK={_idK} rowNumber={_key_} typeBrowse={typeBrowse} />
          :null
          }
        </> 
      )
    })}
    </div>                                      
  )
}




export default RowViewPort;